export default {
  data() {
    return {
      list: [],
      miniList: [],
      total: '',
      total2: '',
      page: 1,
      currentPage: 1,
      page_size: 18
    };
  },

  methods: {
    async fn() {
      console.log(this.$api);
      let res = await this.$api.videoData({
        page: this.page,
        size: this.page_size
      });
      let res2 = await this.$api.miniVideo({
        page: this.page,
        size: this.page_size
      });
      this.total = res.total;
      this.total2 = res2.total;
      this.list = res.list;
      this.miniList = res2.list;
    },

    // 分页
    handleSizeChange(val) {
      this.page_size = val;
      this.fn();
    },

    //选择每页数量
    handleCurrentChange(val) {
      this.page = val;
      this.fn();
    }

  },

  mounted() {
    this.$nextTick(() => {
      this.fn();
    });
  }

};